body {
  background-color: #0d0d0d;
  color: #fff;
  font-family: 'Segoe UI', sans-serif;
  margin: 0;
  overflow-x: hidden;
}

.app-container {
  text-align: center;
  padding: 30px 10px;
}

.main-title {
  font-size: 2.5rem;
  color: #00f5ff;
  margin-bottom: 40px;
  animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
  from { text-shadow: 0 0 10px #00f5ff; }
  to { text-shadow: 0 0 20px #00f5ff, 0 0 30px #0ff; }
}

.slider {
  display: flex;
  transition: transform 0.8s ease-in-out;
  width: 200%;
}

.slide {
  width: 50%;
  padding: 40px;
  box-sizing: border-box;
  transition: opacity 0.4s ease;
}

.slide textarea,
.slide input[type='file'] {
  width: 80%;
  margin: 10px 0;
  padding: 10px;
  background: #1e1e1e;
  border: none;
  border-radius: 8px;
  color: white;
}

.embed-btn,
.extract-btn {
  background-color: #00f5ff;
  border: none;
  padding: 10px 20px;
  margin-top: 15px;
  border-radius: 20px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.embed-btn:hover,
.extract-btn:hover {
  transform: scale(1.05);
  background-color: #0ff;
}

.nav-arrow {
  font-size: 1.5rem;
  margin-top: 20px;
  color: #00f5ff;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.nav-arrow:hover {
  transform: scale(1.2);
}

.slide-left {
  transform: translateX(-50%);
}
